#landing-page #landing-page-logo {
  margin: auto;
  display: block;
  width: 350px; }

#landing-page h1 {
  text-align: center;
  margin-top: 0px; }

#landing-page .center {
  text-align: center; }

#landing-page h2 {
  text-align: center; }

#landing-page .website-title-appear {
  opacity: 0.01; }

#landing-page .website-title-appear.website-title-appear-active {
  opacity: 1;
  -webkit-transition: opacity .9s ease-in-out;
  -o-transition: opacity .9s ease-in-out;
  transition: opacity .9s ease-in-out; }



